/* 字体规范 */
.keyboard {
  display: flex;
  align-items: center;
  width: 100%; }
  .keyboard .content {
    margin: 0.26667rem 0;
    position: relative;
    width: 100%;
    min-height: 4.8rem; }

.keyPause, .keySound, .keyReset {
  position: absolute;
  left: 0.8rem;
  top: 0rem; }
  .keyPause span:first-child, .keySound span:first-child, .keyReset span:first-child {
    width: 0.8rem;
    height: 0.8rem;
    background: #2dc421; }
  .keyPause span:last-child, .keySound span:last-child, .keyReset span:last-child {
    min-width: 0.8rem;
    text-align: center; }

.keySound {
  left: 2.13333rem;
  top: 0; }

.keyReset {
  left: 3.52rem;
  top: 0; }
  .keyReset span:first-child {
    width: 0.8rem;
    height: 0.8rem;
    background: #dd1a1a; }

.keyDrop {
  position: absolute;
  left: 1.28rem;
  top: 1.57333rem; }
  .keyDrop span:first-child {
    width: 2.53333rem;
    height: 2.53333rem;
    background: #5a65f1; }
  .keyDrop span:last-child {
    min-width: 2.53333rem;
    text-align: center; }

.keyRotate, .keyLeft, .keyRight, .keyDown {
  position: absolute;
  left: 6.29333rem;
  top: 0; }
  .keyRotate span:first-child, .keyLeft span:first-child, .keyRight span:first-child, .keyDown span:first-child {
    width: 1.6rem;
    height: 1.6rem;
    background: #5a65f1; }
  .keyRotate span:last-child, .keyLeft span:last-child, .keyRight span:last-child, .keyDown span:last-child {
    min-width: 1.6rem;
    text-align: center; }

.keyLeft {
  left: 4.86667rem;
  top: 1.46667rem; }

.keyRight {
  left: 7.70667rem;
  top: 1.46667rem; }

.keyDown {
  left: 6.29333rem;
  top: 2.8rem; }

.keyDecorate {
  position: absolute;
  left: 6.58667rem;
  top: 1.68rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 1.01333rem;
  height: 1.01333rem;
  font-size: 0; }
  .keyDecorate .keyDecorateCenter {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .keyDecorate .keyDecorateItem {
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0.10667rem 0.10667rem 0.21333rem 0.10667rem;
    border-color: transparent transparent #111; }
