/* 设计稿, 以iphone6为准 */
/* 颜色规范 */
/* 字体规范 */
.character {
  position: relative;
  display: inline-block;
  width: 0.17333rem;
  height: 0.32rem;
  margin-left: 1px;
  background-image: url(../../assets/images/icon.png);
  background-repeat: no-repeat;
  background-size: 5.33333rem 2.48rem;
  background-position: -2.86667rem -0.33333rem;
  overflow: hidden; }

.character_0 {
  background-position: -1rem -0.33333rem; }

.character_1 {
  background-position: -1.18667rem -0.33333rem; }

.character_2 {
  background-position: -1.37333rem -0.33333rem; }

.character_3 {
  background-position: -1.56rem -0.33333rem; }

.character_4 {
  background-position: -1.74667rem -0.33333rem; }

.character_5 {
  background-position: -1.93333rem -0.33333rem; }

.character_6 {
  background-position: -2.12rem -0.33333rem; }

.character_7 {
  background-position: -2.30667rem -0.33333rem; }

.character_8 {
  background-position: -2.49333rem -0.33333rem; }

.character_9 {
  background-position: -2.68rem -0.33333rem; }

.character_ {
  background-position: -2.86667rem -0.33333rem; }

.character_colon {
  width: 0.21333rem;
  background-position: -3.05333rem -0.33333rem;
  animation: colonflash 2000ms infinite; }

.character_sound_off {
  width: 0.33333rem;
  height: 0.28rem;
  background-position: -2rem -1rem; }

.character_sound_on {
  width: 0.33333rem;
  height: 0.28rem;
  background-position: -2.33333rem -1rem; }

.character_pause_on {
  width: 0.33333rem;
  height: 0.28rem;
  background-position: -1rem -1rem;
  animation: pauseflash 500ms infinite; }

.character_pause_off {
  width: 0.33333rem;
  height: 0.28rem;
  background-position: -1.33333rem -1rem; }

@keyframes pauseflash {
  0% {
    background-position: -1rem -1rem; }
  50% {
    background-position: -1rem -1rem; }
  51% {
    background-position: -1.33333rem -1rem; }
  100% {
    background-position: -1.33333rem -1rem; } }

@keyframes colonflash {
  0% {
    background-position: -3.05333rem -0.33333rem; }
  50% {
    background-position: -3.05333rem -0.33333rem; }
  51% {
    background-position: -3.24rem -0.33333rem; }
  100% {
    background-position: -3.24rem -0.33333rem; } }
